<template>
    <div class="scroller">
        
        <!-- scroll graphics: animation is triggered by both scrollin and click -->
        <div class="scroller-right">
        </div>
        <!-- scroll text: description and recommendation  -->
        <div class="scroller-left"></div>
    </div>
</template>

<script>
import "intersection-observer";
import scrollama from "scrollama";
import * as d3 from "d3";
import { toPath, circlePath, rectanglePath } from "@/assets/js/circle2rect.js";
import Stickyfill from "stickyfilljs";

export default {
    name: 'scroller',
    async mounted() {
        // ===============default parameters============================
        const width = window.innerWidth;
        const height = window.innerHeight;
        
        const transitionDuration = 2000;
        const n = 60;

        // ================laod examples=================================
        var visweb;
        await d3.json('./dataset/visweb.json').then(function(data){
            visweb = data;
            // {title, image, url, des}
        });
        // const exampleCounter = visweb.length;

        var section;
        await d3.json('./dataset/section.json').then(function(data){
            section = data;
        });
        const sectionLen = section.length;

        // default color
        function defaultColor(index){
            //return d3.interpolateRainbow(index * 0.06 * height / (25*exampleCounter));
            // return d3.schemeTableau10[index];
            const color = ["#e15759", "#4e79a7"];
            return color[index-1];
        }

        // ================text description===============================
        var rgb;
        const step_text = d3.select(".scroller-left")
            .selectAll("div")
            .data(visweb)
            .enter()
            .append("div")
            .attr("class", "step")
            .attr("id", d => "section-"+d.sectionId)    // section change
            .append("div")
            .attr("class", "step-background")
            .style("background-color", d => {
                    // rgb = d3.color(d3.interpolateRainbow((i+1) * 0.06 * height / (25*exampleCounter)));
                    // section change
                    rgb = d3.color(defaultColor(d.sectionId+1));
                    rgb.opacity = 0.7;
                    return rgb;
            })
        
        // add title and author
        step_text.filter(d => (d.title))
                    .append("p")
                    .attr("class", "exampleTitle")
                    .text(d => d.title)
        
        step_text.filter(d => (d.subtitle))
                    .append("p")
                    .attr("class", "subTitle")
                    .text(d => d.subtitle)
        
        step_text.filter(d => (d.author))
                    .append("p")
                    .attr("class", "author")
                    .text(d => d.author)
        
        // add picture if exist
        step_text.filter(d => (d.image))
                    .append("div")
                    .attr("class", "image_wrapper")
                    .append("img")
                    .attr("class", "image")
                    .attr("src", d => d.image)
        
        // add video if exist
        step_text.filter(d => (d.video) && (d.youtube))
                    .append("div")
                    .attr("class", "video_wrapper")
                    .append("iframe")
                    .attr("class", "video")
                    .attr("src", d => d.video)
                    .attr("frameborder", 0)
                    .attr("allow", "fullscreen; accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture")
        // <iframe width="640" height="360" src="https://www.youtube.com/embed/wDYkvVhky2c" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>

        // add text description
        step_text.filter(d => (d.des))
                    .append("p")
                    .text(function(d){
                        return d.des;
                    })
        
        // add link
        step_text.filter(d => (d.url))
                    .append("h4")
                    .attr("class", "view-link")
                    .html(d=>"<a target='_blank' href="+d.url+"> View → </a>")

        // ================circle part===============================
        const svg = d3.select(".scroller-right")
                    .append("svg")
                    .attr("width", "100%")
                    .attr("height", "100%");

        // circle parameters
        const catalogData = Array.from({length: sectionLen}, (_, i) => {
                    // {rx, ry, r, index}
                    return {
                        "rx": (0.05+0.02*Math.random()) * width,
                        "ry": (i+1) * 0.06 * height, 
                        "r": 0.025 * (1 + Math.random()) * height, 
                        "index": i
                    };
                });

        function initCatalog() {
            svg.selectAll("path")
                .data(catalogData)
                .enter()
                .append("path")
                    .attr("id", d => {return "path_catlog_"+ d.index})
                    .attr("d", d => toPath(rectanglePath(0.7*width, (0.6+d.index*0.05)*height, 0.01*width, 0.04*height, n)))
                    .attr("fill", (d,i) => defaultColor(i+1))
                    .attr("opacity", 0.8)
                    .on("click", function() {
                        // jump to the corresponding text
                        const index = this.getAttributeNode("id").value.slice(12);
                        // document.getElementById("step-"+index).scrollIntoView();
                        document.getElementById("section-"+index).scrollIntoView(); // section change
                    });

            d3.select(".scroller-right")
                .selectAll("div")
                .data(section)
                .enter()
                .append("div")
                .attr("class", "section-catalog")
                .attr("id", d => {return "catalog_"+d.sectionId})
                .style("position", "absolute")
                .style("left", "72.5%")
                .style("top", d => (0.57+d.sectionId*0.05)*height +"px")
                .style("color", d => defaultColor(d.sectionId+1))
                .style("font-size", "larger")
                .append("p")
                .text(d => d.sectionName)
        }
        initCatalog();
        // Bug: can't recover
        
        function createCircles() {
            // section change
            d3.selectAll(".section-catalog")
                .transition()
                .duration(transitionDuration / 2)
                .style("color", d => {
                    rgb = d3.color(defaultColor(d.sectionId+1));
                    rgb.opacity = 0;
                    return rgb;
            });

            svg.selectAll("path")
                .transition()
                .duration(transitionDuration)
                .attr("id", d => {return "path_circle_"+ d.index})
                .attr("d", d => toPath(circlePath(d.rx, d.ry, d.r, n)));
        }

        // ================scroller part==============================
        // use d3 for convience
        const scrolly = d3.select(".scroller");
        const text = scrolly.select(".scroller-left");
        const figure = scrolly.select(".scroller-right");
        const step = text.selectAll(".step");

        // initialize the scrollama
        const scroller = scrollama();

        // generic window resize listener event
        function handleResize() {
            // 1. update height of step elements
            var stepH, stepW, stepTop, stepLeft;
            if (width > 600){
                stepH = Math.floor(height * 0.9);
                stepTop = (height - stepH) / 2;
                stepW = 0.5 * width;
                stepLeft = 0.2 * width;
            } else {
                stepH = height;
                stepTop = 0.1*height;
                stepW = 0.7 * width;
                stepLeft = 0.05 * width;
            }

            step
                .style("height", stepH + "px")
                .style("width", stepW + "px")
                .style("top", stepTop + "px")
                .style("margin-left", stepLeft + "px");

            figure
                .style("height", height + "px")
                .style("top", "0px");

            // 3. tell scrollama to update new element dimensions
            scroller.resize();
        }

        // scrollama event handlers
        function handleStepEnter(response) {
            // response = { element, direction, index }
            const step_section = visweb[response.index].sectionId;

            // add color to current step only
            step.classed("is-active", function(d, i) {
                return i === response.index;
                });
            
            if (response.index == 0) {
                createCircles();
            } else {
                // keep there is only one rectangle
                d3.selectAll("path")
                    .filter(d => d.index != step_section)
                    .transition()
                    .duration(transitionDuration)
                    .attr("id", d => {return "path_circle_"+d.index})
                    .attr("opacity", 0.8)
                    .attr("d", d => toPath(circlePath(d.rx, d.ry, d.r, n)));
            }

            // morph circle to rectangle
            d3.select("#path_circle_"+step_section)
                .transition()
                .duration(transitionDuration)
                .attr("id", "path_rect")
                .attr("d", toPath(rectanglePath(0.095*width, 0, 0.005*width, document.body.clientHeight, n)))
        }

        function handleStepExit(response) {
            console.log(response)
            if (response.index==0) {
                // Bug: no response
                initCatalog();
            }
        }

        // add sticky style
        function setupStickyfill() {
            d3.selectAll(".sticky").each(function() {
            Stickyfill.add(this);
            });
        }

        function init() {
            // set the sticky position style
            setupStickyfill();

            // 1. force a resize on load to ensure proper dimensions are sent to scrollama
            handleResize();

            // 2. setup the scroller passing options
            // 3. bind scrollama event handlers
            scroller
                .setup({
                    step: ".scroller .scroller-left .step",
                    offset: 0.8,
                    debug: false, // scroller debugger
                })
            .onStepEnter(handleStepEnter)
            .onStepExit(handleStepExit);

            // setup resize event
            window.addEventListener("resize", handleResize);
        }

        // kick things off
        init();
    
    }
}



</script>

<style lang="scss">

.scroller-right {
    top: 0;
    width: 100%;
    height: 100%;
    position: fixed;
}

.scroller-left {
    width: 70%;
    margin-left: 10%;
    text-align: -webkit-auto;
    position: relative;
}

.step {
    opacity: 0.25;
    padding: 15% 0;
}

.step.is-active {
    opacity: 1;
}

.step-background {
    padding: 1.8rem 10%;
    border-radius: 0.8em;
}

.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: inherit;
}

.sticky:before,
.sticky:after {
    content: '';
    display: table;
}

.image_wrapper {
    position: relative;
    
}
.image {
    // update: set the same size and add transition
    width: 100%;
    max-height: 60%;
    border-radius: 10px;
    transition: box-shadow 0.2s ease;
}

.image:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

.video_wrapper {
    position: relative;
    padding-bottom: 56.25%;
}

.video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

h4 {
    margin-bottom: 0;
    margin-top: 1rem;
    font-size: 1.6rem;
    font-family: "Dancing Script";
}

.view-link a{
    text-decoration: none;
}

.exampleTitle {
    margin-top: 0;
    margin-bottom: 1.2rem;
    text-align: center;
    font-size: 1.6rem;
    font-weight: 600;
}

.subTitle {
    margin-top: -0.4rem;
    font-style: italic;
    text-align: center;
}

.author {
    margin-top: -0.4rem;
    text-align: center;
}


@media only screen and (max-width: 600px) {
    .exampleTitle {
        font-size: 1.4rem;
    }

}
</style>